
.v3 {
    .editor {
        .pads.show-canvas {
            border: 8px solid black;

            .pad-inner {
                padding-top: 10%;

                .pad-slot:active {
                    .slider-canvas {
                        opacity: 0.8;
                    }
                }

                .pad-toolbar {
                    display: none;
                }
                .slots-and-track {
                    background-color: transparent !important;
                    padding: unset !important;
                }

                .pad-slot.selected {
                    .youtube-info {
                        .sound-thumbnail {
                            background-color: white !important;
                            color: black !important;
                        }
                    }
                }

                .volume-indicator.skinny {
                }

                .pad-key-map {
                    display: none;
                }

                .pad-slot:hover {
                    transform: unset;
                    .volume-indicator.skinny {
                        width: 100%;
                    }
                }
                .pad-slot {
                    box-shadow: unset !important;
                    background-color: #7d7d8552;

                    .youtube-info {
                        //display: none;
                        .sound-thumbnail {
                            background-color: #ffffff30 !important;
                            color: #fdfdfd !important;
                        }
                    }
                }
                .pad-slot.pressed {
                    transform: none !important;
                    background-color: unset !important;
                }
            }
        }
    }
}
.pads-container {
    display: flex;
    flex-wrap: wrap;
}
.pads.delete-mode {
    .pad-inner {
        .pad-slot.deleting, .pad-slot:active {
	    animation: paddelete linear 0.5s infinite;
        }

       
        .pad-slot.empty {
            background-image: none;
        }
        .pad-slot {
            cursor: pointer;
            background-color: red;
            background-image: linear-gradient(
                331deg
                , #2fffb2bf, transparent);
        }
    }
}

.pads.mute-mode {
    .pad-inner {
        .pad-slot.empty {
            background-image: none;
        }
        .pad-slot {
            cursor: pointer;
            background-color: blue;
            background-image: linear-gradient(
                331deg
                , #2fffb2bf, transparent);
        }
    }
}

.pads {
    display: flex;
    position: relative;

    background-color: #ffffff38;
    backdrop-filter: blur(4px);
    margin-right: 10px;

    .pad-canvas {
        position: absolute;
        top: -6px;
        left: -6px;
        border-radius: 8px;
        canvas {
            border-radius: 8px;
        }
    }

    .track {
        margin-top: -3px;
        margin-left: 5px;
        border: none;
        .track-header {
            background-color: transparent !important;
        }

        .track-side-container, .track-tag, .youtube-container {
            display: none;
        }
    }
    .pad-inner {
        border-radius: 8px;
        margin-top: 10px;
        margin-right: 10px;
        position: relative;

        .pad-slot:active {
            .slider-canvas {
                opacity: 1;
            }
        }

        
        .pad-slot {
            .slider-canvas {
                position: absolute;
                bottom: 0px;
                left: 0px;
                opacity: 0;
            }
        }

        .collapse-container {
            cursor: pointer;
            position: absolute;
            background-color: black;
            width: 16px;
            height: 16px;
            top: -20px;
            left: 230px;
            border-radius: 15px;

            .arrow-right {
                width: 0; 
                height: 0; 
                border-top: 3px solid transparent;
                border-bottom: 3px solid transparent;
                border-left: 7px solid #a2a2a2;
                margin-top: 5px;
                margin-left: 5px;
            }

            .arrow-down {
                width: 0; 
                height: 0; 
                margin-top: 5px;
                margin-left: 5px;
                border-left: 3px solid transparent;
                border-right: 3px solid transparent;
                border-top: 7px solid #a2a2a2;
            }
        }

        .pad-toolbar {
            display: flex;
            margin-bottom: 10px;
            font-size: 8pt;

            .waveform-btn.selected {
                background-color: white;
                box-shadow: 0px 0px 4px 4px #ffffffa3;

                .wave {
                    border-bottom-color: black;
                }
            }
            .waveform-btn {
                cursor: pointer;
                display: flex;
                background-color: #ffffff42;
                margin-left: 3px;
                padding-left: 5px;
                padding-right: 5px;
                .wave {
                    width: 0;
                    height: 0;
                    margin-top: 5px;
                    border-bottom: 10px  solid white;
                    border-left: 10px solid transparent;
                }
            }

            .pad-length, .pad-toolbar-btn {
                background-color: #ffffff42;
                color: white;
                padding: 3px;
            }

            .pad-toolbar-btn.selected {
                background-color: white;
                color: black;
                box-shadow: 0px 0px 4px 4px #ffffffa3;
            }

            .pad-toolbar-btn {
                cursor: pointer;
                margin-right: 3px;
            }

            .pad-length {
                margin-left: auto;

                position: relative;
                .length-dropdown {
                    position: absolute;
                    top: 18px;
                    left: 0px;
                    width: 100px;
                    padding: 5px;
                    display: flex;
                    background-color: white;
                    z-index: 10;
                    
                    .length-btn {
                        text-align: center;
                        height: 15px;
                        line-height: 6px;
                        width: 15px;
                        font-size: 12pt;
                        padding: 3px;
                        background-color: black;
                        cursor: pointer;
                        margin-right: 5px;
                    }
                }

                .bars {
                    cursor: pointer;
                    background-color: gray;
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
        }

        .pad-bottom {
            width: 100%;
            overflow: hidden;
        }

       }
        
        .slots-and-track {
            display: flex;
        }
        .pad-slots {
            display: flex;
            flex-wrap: wrap;

            .pad-key-map {
                font-size: 6pt;
                margin-top: 22px;
                margin-left: 4px;
                color: white;
            }
            width: 220px;
        }

        padding: 20px;

        .pad-slot.deleting {
	    animation: paddelete linear 0.5s infinite;
        }

        .pad-slot.muted {
            opacity: 0.2;
        }

        .pad-slot.pressed {
            //transform: scale(1.08);
            .youtube-info {
            }
        }

        .pad-slot.pressed {
            // box-shadow: 0px 0px 2px 2px #ff7600;
            background-color: #ff7600 !important;

            .youtube-info {
                .sound-thumbnail {
                    background-color: #ff8604 !important;
                }

            }
        }

        .pad-slot.empty {
            background-color: #040404b0;
        }

        .pad-slot.selected {
            box-shadow: 0px 0px 4px 4px #b9b9b9b5;
        }

        .pad-slot.hide-names {
            .youtube-info {
                display: none;
            }
        }

        .pad-slot:hover, .pad-slot:active {
            .recording-dot {
                display: block;
            }

            .volume-indicator {
                width: 100%;
            }
        }
        
        .pad-slot.slot-draggable {
            .volume-indicator {
                display: none;
            }
            .youtube-info {
                pointer-events: unset;
                .sound-thumbnail, .youtube-thumbnail {
                    height: 50px !important;
                    width: 100% !important;
                    font-size: 6pt;
                }
            }
        }

        .pad-slot {
            position: relative;
            width: 50px;
            height: 50px;
            margin: 10px;
            background-color: #040404b0;

            .pad-track-number {
                position: absolute;
                font-size: 8pt;
                color: gray;
                bottom: 5%;
                left: 45%;
            }

            .recording-dot.enabled {
                display: block;
                background-color: red;
            }

            .recording-dot {
                display: none;
                position: absolute;
                bottom: 3px;
                left: 3px;
                background-color: #ffffff47;
                z-index: 10000;
                border-radius: 10px;
                width: 10px;
                height : 10px;
            }
            
            .mapped-to-key {
                border-bottom: 1px solid #ffffff42;
                margin-bottom: 3px;
                padding-bottom: 3px;
                .key-map {
                    background-color: black;
                    margin-left: 3px;
                    color: white;
                    padding: 5px;
                }
            }
            


            .volume-indicator.skinny {
                width: 10px;
                right: 0px;
            }
            .volume-indicator {
                position: absolute;
                width: 50px;
                bottom: 0px;
                background-color: #3c57a1;
                opacity: 0.7;
                overflow: hidden;
            }


            .youtube-info {
                pointer-events: none;
                margin-left: 0px;
                width: 100%;
                font-size: 8pt;
                .youtube-thumbnail {
                    margin-top: -4px;
                }

                .sound-thumbnail, .youtube-thumbnail {
                    border: none;
                    // background-image: linear-gradient(45deg, #ff4e6d, transparent);
                    margin-left: 0px;
                    width: 100%;
                    font-size: 8pt;
                    height: 15px;
                    color: black !important;
                }
            }
        }
}

.pads.save-pad {
    .complete-buttons {
        display: flex;
        
        .cancel-btn {
            color: black;
            background-color: red;
            padding: 5px;
            cursor: pointer;
        }
        .done-btn {
            text-align: center;
            flex: 1;
            background-color: #48e8a6;
            color: black;
            padding: 5px;
            cursor: pointer;
        }
    }

    .pack-name {
        background-color:black;
        color: white;
        width: 100%;
        outline: none !important;
        border: none !important;
    }
}

@keyframes paddelete {
    0% { background-color: red; opacity: 1; } 
    50% { opacity: .1; } 
    100% { opacity: 1; }
}

.pad-waveform {
    display: flex;

    .waveform-resizer {
        width: 10px;
        height: 100px;
        cursor: col-resize;
    }
.waveform-buttons {
    flex-wrap: wrap;
    background-color: black;
    width: 210px;
    
    .sample-tempo {
        .warp-button.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 2px 2px white;
        }

        .warp-button {
            display: block !important;
            line-height: 8px;
            width: unset;
            text-align: center;
            height: 20px;
            margin-top: 0px;
            opacity: 1;
            background-color: transparent;
            color: white;
            border: 1px solid white;
        }
        display: flex;
        line-height: 20px !important;
        margin-top: 5px !important;
        margin-right: 5px !important;
        margin-left: auto !important;
        // flex: 1;
    }
    .add-slice-button {
        background-image: url(/new-slice-icon.svg);
        width: 30px !important;
        height: 40px !important;
    }

    .delete-slice {
        background-image: url(/delete-slice-icon.svg);
        margin-top: 7px !important;
        background-size: contain !important;
        background-repeat: no-repeat;
        width: 15px !important;
        height: 24px !important;
    }

    .add-slice-button, .delete-slice {
        margin-right: 5px !important;
        background-size: cover;
        .inner {
            display: none;
        }
    }
    .details-button {
        background-color: unset;
    }
    .reverse {
        padding-left: 7px !important;
    }
    .details-button, .sample-tempo, .gate-btn, .reverse {
        background-color: unset;
        border: unset;
        color: unset;
        margin: 0px;
        height: unset;
        line-height: unset;
        padding: unset;
        width: unset;
        font-size: 10pt;
        .inner {
            padding: 0px;
            line-height: unset;
        }
    }
    .auto-slice-btn, .crop-btn, .onsets-btn, .zoom-out-btn {
        display: none;
    }
}
}


.pulled-pad.expanded {
    z-index: 100000000000000000000 !important;
    .pads {
        background-color: #222222f5;
    }
}

.visualizer {
    height: 100px;
    background-color: #000000b0;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    padding-top: 10px;
    
    .play-tick {
        position: absolute;
        top: 0px;
        left: 0px;
        height: 100px;
        background-color: #404040;
        background-image: linear-gradient(90deg, #10101000, #9eedff59);
        opacity: 0.7;
        z-index: 0;
    }
    
    .track-steps.selected {
        background-color: #ffffff36;
    }
    .track-steps {
        z-index: 1;
        padding-top: 5px;
        position: relative;
        margin-bottom: 4px;
        width: 100%;
        height: 4px;
        .track-step {
            top: 0px !important;
            position: absolute;
        }
    }
}

